import { AccessTime } from "@material-ui/icons";
import { pathOr } from "ramda";
import React from "react";
import { If } from "react-if";
import { nextOpenHour } from "../../services/hoursUtils";

import { Merchant } from "../../services/models";
import { getTimeLabelFromOffset, isEmptyOrNil, isStoreOpen } from "../../utils";
import { CardHighlightChip } from "../Classifieds/styledComponents";

type MerchantOrderAheadChipProps = {
  merchant: Merchant;
};

const MerchantOrderAheadChip = (props: MerchantOrderAheadChipProps): JSX.Element | null => {
  const { merchant } = props;

  const isMerchanStoreOpen = isStoreOpen(merchant, new Date());

  const pickupStartsAtOffset = pathOr(0, ['orderingConfig', 'pickupStartsAtOffset'], merchant);
  const openFromMinOffset = !isMerchanStoreOpen ? nextOpenHour(merchant) : null;
  const storeTimingsLabel = !isEmptyOrNil(openFromMinOffset)
    ? getTimeLabelFromOffset(openFromMinOffset + pickupStartsAtOffset)
    : "";

  const isOrderAheadVisible = !isMerchanStoreOpen && merchant.orderingConfig?.orderAhead && storeTimingsLabel

  return (
    <If condition={isOrderAheadVisible}>
      <CardHighlightChip
        icon={<AccessTime />}
        label={`Store opens at ${storeTimingsLabel}`}
        color="secondary"
        size="small"
        variant="filled"
        sx={{ backgroundColor: "#E34D28" }}
      />
    </If>
  );
};

export default MerchantOrderAheadChip;
