import React from "react";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Paper from "@material-ui/core/Paper";
import Typography, { TypographyProps } from "@material-ui/core/Typography";
import StarIcon from "@material-ui/icons/Star";
import { createStyles, makeStyles } from "@material-ui/core";
import { If } from "react-if";
import classNames from "classnames";

import { MerchantRating } from "../../bos_common/src/types/MerchantType";
import { pluralString } from "../../utils";

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      display: 'flex',

      '& .MuiPaper-root.list-item-icon-container': {
        marginLeft: 'auto',
        padding: '0.25rem',
      },

      '& .MuiPaper-root.list-item-icon-container.star-only': {
        boxShadow: 'none',
      },

      '& .MuiListItemIcon-root': {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
      },
    },
  }))


interface MerchantRatingInfoProps {
  merchantRating?: MerchantRating;
  typographyProps?: TypographyProps<"span">;
  starOnly?: boolean;
}

export const MerchantRatingInfo = (props: Readonly<MerchantRatingInfoProps>): JSX.Element | null => {
  const classes = useStyles();
  const { merchantRating, typographyProps = {}, starOnly } = props;

  const paperClassNames = classNames({
    'list-item-icon-container': true,
    'star-only': starOnly
  })

  return (
    <If condition={merchantRating && merchantRating.overallRating > 0 && merchantRating?.reviewCount >= 10}>
      <div className={classes.root} style={{ width: starOnly ? 'auto' : '100vw' }}>
        <If condition={!starOnly}>
          <Typography {...typographyProps}>{pluralString(merchantRating?.reviewCount || 0, 'review')}</Typography>
        </If>
        <Paper classes={{ root: paperClassNames }}>
          <ListItemIcon>
            <StarIcon color="primary" fontSize="small" />
            <Typography variant="subtitle1" {...typographyProps}>{merchantRating?.overallRating?.toFixed(2)}</Typography>
          </ListItemIcon>
        </Paper>
      </div>
    </If>
  );
}

export default MerchantRatingInfo;
