import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { If, Then } from 'react-if';
import { pipe, propOr, split, without, slice, take } from 'ramda';
import { CardContent, Typography, Card, CardActionArea, Box } from '@material-ui/core';

// src
import { Merchant } from '../../services/models';
import PlaceholderImage from "../../assets/images/merchant-placeholder.png";

import { isEmptyOrNil, withFallbackPath } from '../../utils';
import { EllipsizedText, StyledExpenseChip, StyledGreenChip } from './styledComponents';
import { ClassifiedEntry } from '../../bos_common/src/types/ClassifiedType';
import { getBigSavingsValue, isBigSavingsClassified } from './utils';
import { useCardStyles } from './styles';
import MerchantCategoriesList from '../common/MerchantCategoriesList';
import renderMerchantRewards from "../common/MerchantRewards";
import ArrowDownFilledIcon from '../../assets/icons/ArrowDownFilledIcon';
import { MERCHANT_EXPENSE_LEVELS } from '../../bos_common/src/constants';
import MerchantOrderAheadChip from '../common/MerchantOrderAheadChip';

const getCardSubtitles = (merchant: Merchant) => {
  const subtitles = [];
  const points = renderMerchantRewards(merchant);
  if (points) {
    subtitles.push(points)
  }
  return subtitles;
}

const getCardBackgroundSrc = (merchant: Merchant) => {
  if (!isEmptyOrNil(merchant.heroPhoto)) return merchant.heroPhoto
  return PlaceholderImage
}

const ClassifiedMerchantCard = ({ entry }: { entry: ClassifiedEntry }): JSX.Element => {
  const classes = useCardStyles();

  const merchant = entry.merchant!;
  const subtitles = getCardSubtitles(merchant)
  const categoriesList: string[] = useMemo(() => {
    return pipe(propOr('', 'category'), split(','), slice(0, 3), without([' ', '']), take(1))(merchant)
  }, [merchant]);
  const cardBackgroundSrc = getCardBackgroundSrc(merchant)
  const chipText = isBigSavingsClassified(entry) ? `Up to ${getBigSavingsValue(entry)} Exclusive Saving` : ''

  const BackgroundImage = (props: any) => (
    <img
      className="card-background-img"
      src={cardBackgroundSrc + "_medium"}
      onError={props.onError}
    />
  );
  const EnhancedBackgroundImage = withFallbackPath(BackgroundImage, cardBackgroundSrc)

  const expenseLevel = MERCHANT_EXPENSE_LEVELS?.find((item: any) => item.value === merchant?.expenseLevel)

  return (
    <Card elevation={3} className={classes.root}>
      <CardActionArea
        className="card-action-container"
        component={Link}
        to={{
          pathname: `/${merchant.username}`,
          state: { from: window.location.pathname },
        }}
      >
        <EnhancedBackgroundImage />
        <div className='overlay' />

        <Box className="card-highlight-text-container">
          <If condition={!isEmptyOrNil(chipText)}>
            <StyledGreenChip
              size="small"
              icon={<ArrowDownFilledIcon sx={{ width: 18, height: 18 }} />}
              label={<EllipsizedText>{chipText}</EllipsizedText>} variant="filled"
            />
          </If>

          {/* <MerchantOrderAheadChip merchant={merchant} /> */}
        </Box>

        <CardContent className="card-content">
          <Typography className="card-title" title={merchant.officialName} component="div" variant="h6">
            {merchant.officialName}
          </Typography>

          <div className="card-info-container">
            <If condition={!isEmptyOrNil(subtitles)}>
              {subtitles.map<React.ReactNode>((t, index) => <React.Fragment key={`${index}-${Math.random() * 100}`}>{t}</React.Fragment>)}
            </If>
          </div>

          <Box sx={{ pr: 2, mt: 1, display: "flex", alignItems: "flex-end" }}>
            <If condition={!isEmptyOrNil(merchant?.expenseLevel) && !isEmptyOrNil(expenseLevel)}>
              <Then>
                <StyledExpenseChip
                  label={expenseLevel?.label || ""}
                  variant="filled"
                  size="small"
                  sx={{ marginRight: 1 }}
                />
              </Then>
            </If>
            <If condition={!isEmptyOrNil(categoriesList)}>
              <Then>
                <MerchantCategoriesList categoriesList={categoriesList} />
              </Then>
            </If>
          </Box>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClassifiedMerchantCard