import React, { useState } from "react";
import { Link } from "@material-ui/core";
import { ArrowRightAlt } from "@material-ui/icons";

import LearnMoreDialog from "./LearnMoreDialog";
import RewardsInfoContent from "./common/RewardsInfoContent";

const DeserveRewardsDialog = () => {
  const [isDialogOpen, setDialogOpen] = useState(false);

  return (
    <>
      <Link
        color="inherit"
        onClick={() => setDialogOpen(true)}
        className="learnMore"
      >
        Learn More <ArrowRightAlt fontSize="small" sx={{ marginLeft: 1 }} />
      </Link>
      <LearnMoreDialog
        isOpen={isDialogOpen}
        onCancel={() => setDialogOpen(false)}
        isMultiColumn={false}
      >
        <RewardsInfoContent />
      </LearnMoreDialog>
    </>
  );
};

export default DeserveRewardsDialog;
