import React from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core';

import { MerchantPromotion } from '../../bos_common/src/types/MerchantPromotionType';
import { CardHighlightChip, EllipsizedText } from '../Classifieds/styledComponents';
import { getPromotionChipStyleConfig } from '../../utils';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& .label-container": {
        display: 'grid',
        gridTemplateColumns: '15px 1fr',
        gridColumnGap: '10px',

        alignItems: 'center',
        justifyContent: 'center'
      }
    }
  })
);


const MerchantPromotionChip = (props: { promotion: MerchantPromotion }) => {
  const { promotion } = props;
  const classes = useStyles();

  const { icon: PromotionIcon, backgroundColor } = getPromotionChipStyleConfig(promotion)

  return (
    <CardHighlightChip
      className={classes.root}
      label={(
        <div className='label-container'>
          <PromotionIcon sx={{ color: "white", width: 18, height: 18 }} /><EllipsizedText>{promotion.oneliner}</EllipsizedText>
        </div>
      )}
      color="secondary"
      size='small'
      variant='filled'
      sx={{ backgroundColor }}
    />
  )
}

export default MerchantPromotionChip;