import React from "react";
import { Alert } from "@material-ui/core";
import { Order } from "../../services/models";
import { Link } from "react-router-dom";


export default function OrderReviewCTA({ order }: {
  order: Order,
}): React.ReactElement | null {
  return (
    <Alert severity="warning" elevation={3}>
      Your opinion matters! Review your order&nbsp;
      <Link to={{
        pathname: `/order/${order?.id}/reviews`,
        state: { from: window.location.pathname }
      }}>
        here
      </Link>.
    </Alert>
  )
}