import React, { useState, useMemo, useContext, useEffect } from 'react';
import AliceCarousel from 'react-alice-carousel';
import { Box, Stack, Theme, Typography, useMediaQuery } from '@material-ui/core';
import { flatten, length, pathOr, pipe, pluck, take, without } from 'ramda';
import { ChevronLeft, ChevronRight } from '@material-ui/icons';
import { If } from 'react-if';

import { ClassifiedDetailsType, ClassifiedEntry, ClassifiedKey } from '../../bos_common/src/types/ClassifiedType';
import { CarousellNavigationButton, CarousellWrapper } from './styledComponents';
import ClassifiedShowAllCard from './ClassifiedShowAllCard';
import ClassifiedDrawer from './ClassifiedDrawer';
import CountBox from '../common/CountBox';

import { getClassifiedItemsElementsList, getPromotionsElementsList, getPromotionsList } from './utils';
import { UserContext } from '../../bos_common/src/context/UserContext';

import 'react-alice-carousel/lib/alice-carousel.css';
import { isEmptyOrNil } from '../../utils';
import { useWindowStorage } from '../../bos_common/src';

const sortClassifiedEntriesBySortOrder = (unsortedList: ClassifiedEntry[], sortOrder: number[]) => {
  const sortedList = unsortedList
    .map((item: ClassifiedEntry, index: number) =>
    ({
      item,
      sortKey: sortOrder[index] ?? 9999
    }))
    .sort((a, b) => a.sortKey - b.sortKey)
    .map(({ item }) => ({ ...item }));

  return sortedList
}

const generateSortOrder = (listLength: number) => {
  const sortOrder = Array.from(Array(listLength).keys());
  sortOrder.sort(() => Math.random() - 0.5);
  return sortOrder;
}

const ClassifiedCarousel = ({ list = [] }: { list: ClassifiedEntry[] }) => {
  const [sortedClassifiedList, setSortedClassifiedList] = useState<ClassifiedEntry[]>([]);
  const { user } = useContext(UserContext)

  const classified: ClassifiedDetailsType = pathOr({}, [0, 'details'], list)
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
  const [isDrawerOpen, toggleDrawer] = useState<boolean>(false);
  const [sortOrder, setSortOrder] = useWindowStorage<number[] | null>(classified.name, null, 'sessionStorage');

  // on mobile show the first 3 items, else show the first 5 items
  const isHotDealClassified = classified.key === ClassifiedKey.hotDeal;
  const totalItems = isHotDealClassified ? pipe(pluck('promotions'), flatten, without([undefined]), length)(getPromotionsList(list, user)) : list.length

  useEffect(() => {
    if (list.length < 1) return;

    let newSortOrder = sortOrder;
    if (!newSortOrder) {
      newSortOrder = generateSortOrder(list.length);
      setSortOrder(newSortOrder);
    }
    const sortedList = sortClassifiedEntriesBySortOrder(list, newSortOrder);
    setSortedClassifiedList(sortedList);
  }, [list]);

  const cardsLimit = isMobile ? 3 : 5

  const classifiedsList = useMemo(() => {
    if (sortedClassifiedList.length < 1) {
      return [];
    }

    const partialList: ClassifiedEntry[] = take(cardsLimit, sortedClassifiedList);
    const cards: (JSX.Element)[] = isHotDealClassified ? take(cardsLimit, getPromotionsElementsList(partialList, user)) : getClassifiedItemsElementsList(partialList);

    // Add see more card to Carousal when cardlimit is less than totalItems
    if (totalItems > cardsLimit) {
      cards.push(<ClassifiedShowAllCard onCardClick={() => toggleDrawer(true)} />)
    }
    return cards;
  }, [sortedClassifiedList, cardsLimit, user, totalItems]);

  const RenderLeftCarouselScrollHandler = ({ isDisabled }: { isDisabled: boolean }) => {
    if (isDisabled) return null
    return (
      <CarousellNavigationButton size='small' color='primary' disabled={isDisabled}>
        <ChevronLeft />
      </CarousellNavigationButton>
    )
  }

  const RenderRightCarouselScrollHandler = ({ isDisabled }: { isDisabled: boolean }) => {
    if (isDisabled) return null
    return (
      <CarousellNavigationButton size='small' color='primary' disabled={isDisabled}>
        <ChevronRight />
      </CarousellNavigationButton>
    )
  }

  if (isEmptyOrNil(classifiedsList)) return null;

  return (
    <Box sx={{ mt: 2 }}>
      <Stack direction={"row"} alignItems={'center'} justifyContent={'space-between'} sx={{ mb: 2, zIndex: 0 }}>
        <div>
          <Typography variant='h6' sx={{ display: 'flex', alignItems: "center", fontWeight: 700 }}>
            {classified.name}
            &nbsp;
            <CountBox count={totalItems} />
          </Typography>
          <Typography variant='subtitle2' fontWeight="600" lineHeight="16px" color="textSecondary">{classified.oneliner}</Typography>
        </div>
      </Stack>

      <CarousellWrapper>
        <AliceCarousel
          autoWidth
          disableDotsControls
          mouseTracking
          disableButtonsControls={isMobile}
          items={classifiedsList}
          renderPrevButton={RenderLeftCarouselScrollHandler}
          renderNextButton={RenderRightCarouselScrollHandler}
          paddingRight={10}
        />
      </CarousellWrapper>

      <If condition={isDrawerOpen}>
        <ClassifiedDrawer
          open={isDrawerOpen}
          setDrawerOpen={toggleDrawer}
          list={sortedClassifiedList}
        />
      </If>
    </Box>
  )
}

export default ClassifiedCarousel