interface SearchStateType {
  sortBy: string;
  price: number | undefined;
  name: string;
  selectedCategory: string;
}

const MerchantDiscoveryPage = (): React.ReactElement => {
  const classes = useStyles();
  const { user } = useContext(UserContext);

  const [searchState, setSearchState] = useWindowStorage<SearchStateType>(
    LOCALSTORAGE_MARKETPLACE_APP_KEYS.MARKETPLACE_DISCOVERY_SEARCH_STATE,
    {
      sortBy: "",
      price: -1,
      name: "",
      selectedCategory: "",
    },
    "sessionStorage"
  );

  const [storeLocations, setStoreLocations] = useState<StructuredAddress[]>();
  const [showOnlineOnlyMerchants, setShowOnlineOnlyMerchants] = useState<boolean>(false);

  const isMobile = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("sm")
  );

  const isTabletView = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("md")
  );

  const [{ data: merchantsList, loading: isFetchingMerchants }, executeCall] =
    useAxios<Merchant[]>(
      { url: `/merchants/search`, method: "GET" },
      { manual: true }
    );

  useEffect(() => {
    scrollToTop();

    trackRPEvent(
      PIXEL_EVENTS_TYPE.visitedSanMateoMPDiscovery,
      { user }
    )
  }, [])

  // execute Search Call where search state is updated
  useEffect(() => {
    executeCall({
      params: {
        limit: 100, // provided limit 1000 because a param with null value is not sent in the request
        zipcode: "94401,94404",
        ...(!isEmptyOrNil(searchState.name) && { name: searchState.name }),
        ...(!isEmptyOrNil(searchState.selectedCategory) && {
          category: searchState.selectedCategory,
        }),
        ...(searchState.price && searchState.price > 0 && {
          expenseLevel: searchState.price,
        }),
      },
    });
  }, [searchState]);

  const filteredMerchantsList = useMemo(
    () =>
      merchantsList?.filter((i) => {
        return showOnlineOnlyMerchants ? isMerchantOnline(i) : true;
      }) || [],
    [merchantsList, showOnlineOnlyMerchants]
  );

  useEffect(() => {
    if (!filteredMerchantsList) return;
    const storeLocations: StructuredAddress[] = filteredMerchantsList
      .filter(
        (item) =>
          item.structuredAddress &&
          item.structuredAddress.lat &&
          item.structuredAddress.lng
      )
      .map<StructuredAddress>(
        (item) =>
        ({
          ...item.structuredAddress,
          id: item.username,
          title: item.officialName,
          subtitle: item.description,
        } || {})
      );

    setStoreLocations([...storeLocations]);
  }, [filteredMerchantsList]);

  const handleChange = (key: string, value: any) => {
    setSearchState((s) => ({
      ...s,
      [key]: value,
    }));
  };

  const onChangeQuery = (query: string): void => {
    handleChange("name", query);
    scrollToTop("marketplace-page");
  };

  return (
    <div className={classes.root} id="marketplace-page">
      <If condition={!isMobile}>
        <Navbar>
          <div className="search-box">
            <StoreSearchBox
              onChangeQuery={onChangeQuery}
              placeholder="Search in San Mateo..."
              defaultValue={searchState.name}
              leftChild={
                <IconButton sx={{ p: 0 }} component={Link} to={"/sanmateo"}>
                  <NavigateBefore fontSize="large" color="primary" />
                </IconButton>
              }
            />
          </div>
        </Navbar>
      </If>

      <Grid container direction={isTabletView ? 'column-reverse' : 'row'} alignItems="flex-start">
        <Grid item sm={12} xs={12} md={6} sx={{ width: "100%" }} >
          <ColoredPaper variant="outlined" className="content">
            <SimpleLoader loading={isFetchingMerchants} />
            <CardContent>
              <Stack
                direction="row"
                alignItems="center"
                spacing={2}
                overflow="auto"
                sx={{ marginBottom: 2 }}
              >
                {MERCHANTS_CATEGORIES_LIST.map((category) => {
                  const isCategorySelected =
                    searchState.selectedCategory === category.value;
                  return (
                    <StyledChip
                      key={category.value}
                      label={category.label}
                      icon={
                        isCategorySelected && category.selectedIcon
                          ? category.selectedIcon
                          : category.icon
                      }
                      variant={"filled"}
                      className="filters-chip"
                      color={isCategorySelected ? "primary" : "default"}
                      onClick={() =>
                        handleChange(
                          "selectedCategory",
                          isCategorySelected ? "" : category.value
                        )
                      }
                    />
                  );
                })}
              </Stack>
              <Stack direction="row" alignItems="center" spacing={2}>
                <CustomizedChip
                  variant="filled"
                  color="default"
                  className={classNames({
                    openNowChip: true,
                    active: showOnlineOnlyMerchants,
                  })}
                  icon={<OpenNowIcon />}
                  label="Open Now"
                  onClick={() =>
                    setShowOnlineOnlyMerchants(!showOnlineOnlyMerchants)
                  }
                />

                <Box sx={{ minWidth: 130 }}>
                  <FormControl
                    fullWidth
                    variant="outlined"
                    size="small"
                    className="filterDropdown"
                  >
                    <Select
                      labelId="expense-level"
                      value={searchState.price}
                      onChange={(e) => handleChange("price", e.target.value)}
                      placeholder="Price Range"
                      displayEmpty
                    >
                      <MenuItem value={-1}>
                        Price Range
                      </MenuItem>
                      {MERCHANT_EXPENSE_LEVELS.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Box>
              </Stack>
            </CardContent>

            <CardContent>
              <Typography variant="subtitle1" component={"div"} gutterBottom>
                Filtered
                <Typography
                  component={"span"}
                  color="primary"
                >
                  &nbsp;
                  {filteredMerchantsList.length}&nbsp;
                </Typography>
                local&nbsp;
                {searchState.selectedCategory && <Typography
                  component={"span"}
                  color="primary">
                  {searchState.selectedCategory}&nbsp;
                </Typography>}
                merchants
              </Typography>

              <StoresListDetailed merchants={filteredMerchantsList} capToMD={true} />
            </CardContent>

            <If condition={isMobile}>
              <FabContainer
                alignment="right"
                fabStyle={{ padding: "24px 16px 16px" }}
                fadeTop={isMobile}
              >
                <MenuDrawer
                  menuButtonProps={{ color: "primary", variant: "contained" }}
                />
              </FabContainer>
            </If>
          </ColoredPaper>
        </Grid>

        <Grid item sm={12} xs={12} md={6} className="mapContainer">
          <div className="banner">
            <If condition={isMobile}>
              <div className="search-box">
                <StoreSearchBox
                  onChangeQuery={onChangeQuery}
                  placeholder="Search in San Mateo..."
                  defaultValue={searchState.name}
                  leftChild={
                    <IconButton sx={{ p: 0 }} component={Link} to={"/sanmateo"}>
                      <NavigateBefore fontSize="large" color="primary" />
                    </IconButton>
                  }
                />
              </div>
            </If>
            <EmbededMap storeLocations={storeLocations || []} height={isTabletView ? "250px" : "100vh"} />
          </div>
        </Grid>
      </Grid>

      <AppFooter />
    </div>
  );
};

export default MerchantDiscoveryPage;
