import React, { useContext, useMemo, useState } from "react"
import {
  CardContent, MenuItem, Button, makeStyles, Theme, createStyles, Alert, Typography,
  Divider, Paper, TextField, InputAdornment
} from "@material-ui/core"
import { format } from "date-fns"
import { all, any, lensPath, over, pathOr } from "ramda";

import BottomDrawer from "../../bos_common/src/components/BottomDrawer"
import { Merchant, SpecialHours } from "../../services/models";
import { getMinutesOffset, isEmptyOrNil, isStoreOpen } from "../../utils";
import { DAYS_OF_WEEK } from "../../bos_common/src";
import { If } from "react-if";
import { VisitedMerchantContext } from "../../context/VisitedMerchantContext/VisitedMerchantContext";
import { DateRange, Schedule } from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      paddingTop: theme.spacing(2),

      '& .scheduleForm': {
        display: 'flex',
        flexDirection: "column",
        width: '100%',
        maxWidth: 450,
        minWidth: 60,
        margin: '0 auto',

        '& .MuiFormControl-root': {
          marginBottom: theme.spacing(2),
        }
      },
      '& .scheduleButton': {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
      }
    },
  }),
);

const formatMinutes = (m: number) => ("0" + m).slice(-2)

const findSpecialDays = (d: Date, merchantSpecialHours: SpecialHours[]): SpecialHours[] | undefined => {
  const dayOfWeek = DAYS_OF_WEEK[d.getDay()];
  const specialTime: SpecialHours[] | undefined = merchantSpecialHours.filter(({ dayOfWeek: d }: SpecialHours) => d === dayOfWeek);
  return specialTime;
}

const DATE_FORMAT = 'EEE, MMM d'

type ScheduleOrderDrawerProps = {
  defaultValue?: Date,
  merchant: Merchant | undefined,
  open: boolean,
  setOpen: (open: boolean) => void,
  onSchedule: (d: Date) => void,
  onClose: () => void,
}

const ScheduleOrderDrawer = (props: ScheduleOrderDrawerProps): React.ReactElement => {
  const classes = useStyles()

  const { defaultValue, merchant } = props;
  const now = new Date()
  const currentMinsOffset = getMinutesOffset(now)

  const todayTS = new Date(now).setHours(0, 0, 0, 0);

  const defaultDate = new Date(defaultValue || now);
  const [pickupDateTS, setDate] = useState<number>(new Date(defaultDate).setHours(0, 0, 0, 0));
  const [pickupMinuteOffset, setMinuteOffset] = useState<number>(getMinutesOffset(defaultDate))
  const [errorMsg, setErrorMsg] = useState<string>("")
  const { isMerchantOrderingAvailable } = useContext(VisitedMerchantContext)

  const merchantSpecialHours = pathOr([], ['hours', 'customizations'], merchant)

  const getPickUpTimeOptions = () => {
    let timeOptions: [string, number][] = []
    const AMPM: string[] = ['AM', 'PM']
    const gap = 20
    let offset = 0
    AMPM.map((val: string) => {
      for (let i = 0; i < 12; i++) {
        const hour = (i === 0) ? 12 : i
        let j
        for (j = 0; j < 60 - gap; j += gap) {
          if (!isTimeItemDisabled(offset))
            timeOptions.push([`${hour}:${formatMinutes(j)} - ${hour}:${j + 20} ${val}`, offset])
          offset += gap
        }
        if (!isTimeItemDisabled(offset))
          timeOptions.push([`${hour}:${formatMinutes(j)} - ${i + 1}:00 ${i === 11 ? val === 'AM' ? 'PM' : 'AM' : val}`, offset])
        offset += gap
      }
    })
    if (timeOptions.length === 0) {
      timeOptions.push(['No time slot available', 0])
    } else {
      timeOptions = over(lensPath([0, 0]), (x: string) => `ASAP (${x})`, timeOptions)
    }

    return timeOptions
  }

  const isOpenOnDay = (d: Date) => {
    const specialDays = findSpecialDays(d, merchantSpecialHours)
    if (!specialDays) return true

    const isOpen = (specialDay: SpecialHours) => !(specialDay.toMinOffset === 0 || specialDay.fromMinOffset === 0)
    return all(isOpen, specialDays)
  }

  // Only support the same day order ahead right now
  const getPickupDateOptions = () => {
    const dateOptions: [string, Date, boolean][] = []
    const d = new Date(todayTS);
    const numDaysToOrderAhead = merchant?.orderingConfig?.numDaysToOrderAhead || 0;

    const getDateLabel = (index: number, label: string) => {
      if (index > 1) return label
      return index === 0 ? 'Today' : 'Tomorrow'
    }

    for (let i = 0; i < 1 + numDaysToOrderAhead; i++) {
      dateOptions.push([getDateLabel(i, format(d, DATE_FORMAT)), new Date(d), isOpenOnDay(d)])
      d.setDate(d.getDate() + 1)
    }
    return dateOptions
  }

  const isTimeSelectionInvalid = (timeValue: number): boolean => {
    // if merchant hours or if default to and min time are null then do not disable the time
    const defaultFromMinOffset = pathOr(null, ["merchant", "hours", "defaultFromMinOffset"], props)
    const defaultToMinOffset = pathOr(null, ["merchant", "hours", "defaultToMinOffset"], props)
    const pickupStartsAtOffset = pathOr(0, ["merchant", "orderingConfig", "pickupStartsAtOffset"], props)
    const pickupEndsAtOffset = pathOr(0, ["merchant", "orderingConfig", "pickupEndsAtOffset"], props)
    if (!props.merchant?.hours || isEmptyOrNil(defaultFromMinOffset) || isEmptyOrNil(defaultToMinOffset)) return false

    // timeValue should lie between the defaultFromMinOffset and defaultToMinOffset
    const isInvalidTimeValue = timeValue < (defaultFromMinOffset + pickupStartsAtOffset) || timeValue >= (defaultToMinOffset - pickupEndsAtOffset)

    return isInvalidTimeValue
  }

  const isTimeSelectionInvalidForSpecialHours = (specialDays: SpecialHours[] = [], timeValue: number) => {
    // timeValue should lie between the toMinOffset and fromMinOffset
    const isValidTimeValue = (specialDay: SpecialHours) => {
      const { toMinOffset, fromMinOffset } = specialDay
      return fromMinOffset <= timeValue && toMinOffset >= timeValue
    }
    return !any(isValidTimeValue, specialDays)
  }

  const isTimeItemDisabled = (timeValue: number) => {
    const specialDays = findSpecialDays(new Date(pickupDateTS), merchantSpecialHours)
    if (pickupDateTS === todayTS) {
      const invalidCheck = !isEmptyOrNil(specialDays)
        ? isTimeSelectionInvalidForSpecialHours(specialDays, timeValue)
        : isTimeSelectionInvalid(timeValue)
      return (timeValue < currentMinsOffset) || invalidCheck
    }

    return !isEmptyOrNil(specialDays)
      ? isTimeSelectionInvalidForSpecialHours(specialDays, timeValue)
      : isTimeSelectionInvalid(timeValue)
  }

  const handleTimeChange = (event: any) => {
    setMinuteOffset(event.target.value);
    setErrorMsg("")
  };

  const handleDateChange = (event: React.ChangeEvent<{ value: string }>) => {
    setDate(Number(event.target.value));
    setErrorMsg("")
  };

  const onClickSchedule = () => {
    if (isTimeItemDisabled(pickupMinuteOffset)) {
      setErrorMsg("Please choose a valid pick up time")
    } else {
      const pickupFinalDateTime = new Date(pickupDateTS + pickupMinuteOffset * 60 * 1000)
      props.onSchedule(pickupFinalDateTime)
      props.setOpen(false)
      setErrorMsg("")
    }
  }

  const pickupDateOptions = useMemo(() => getPickupDateOptions(), []);
  const pickupTimeOptions = useMemo(() => getPickUpTimeOptions(), [pickupDateTS]);

  return (
    <BottomDrawer
      {...props}
      title={''}
      onClose={props.onClose}
    >
      <Paper className={classes.root}>
        <Typography variant="h6" align="center" >Schedule Pickup Time</Typography>
        <Divider sx={{ my: 1 }} />
        <CardContent>
          <form className='scheduleForm'>
            <TextField
              id="pick-date"
              select
              label="Date"
              value={`${pickupDateTS}`}
              onChange={handleDateChange}
              variant="outlined"
              fullWidth
              InputProps={{
                startAdornment: <InputAdornment position="start"><DateRange color="primary" /></InputAdornment>,
              }}
            >
              {pickupDateOptions.map((val: [string, Date, boolean]) => {
                return (
                  <MenuItem key={val[0]} value={val[1].getTime()} disabled={!(val[2])}>
                    {val[0]}
                  </MenuItem>
                )
              })}
            </TextField>
            <TextField
              id="pick-time"
              select
              label="Time"
              value={pickupMinuteOffset}
              onChange={handleTimeChange}
              variant="outlined"
              fullWidth
              error={isTimeItemDisabled(pickupMinuteOffset)}
              InputProps={{
                startAdornment: <InputAdornment position="start"><Schedule color="primary" /></InputAdornment>,
              }}
              SelectProps={{
                displayEmpty: true
              }}
            >
              {pickupTimeOptions.map((val: [string, number]) => {
                return <MenuItem key={val[1]} value={val[1]}>
                  {val[0]}
                </MenuItem>
              })}
            </TextField>
            <If condition={isMerchantOrderingAvailable && !isStoreOpen(merchant, new Date())}>
              <Typography variant="subtitle2" color="GrayText" textAlign="center">
                {merchant?.officialName} is closed now. To place an order, you need to select a time for pick-up in the future.
              </Typography>
            </If>
            <Button
              color="primary"
              variant="contained"
              onClick={onClickSchedule}
              fullWidth
              className='scheduleButton'>
              Confirm
            </Button>
            {errorMsg && <Alert severity="warning">{errorMsg}</Alert>}
          </form>
        </CardContent>
      </Paper>
    </BottomDrawer>
  )
}

export default ScheduleOrderDrawer