import React from "react";
import {
  Dialog,
  makeStyles,
  Theme,
  createStyles,
  Button,
  useMediaQuery,
} from "@material-ui/core";
import { Close } from "@material-ui/icons";
import { isEmptyOrNil } from "../utils";
import { If } from "react-if";
import { RewardsContentStyled } from "../styles/Common.style";
import classNames from "classnames";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& .MuiPaper-root": {
        [theme.breakpoints.down("md")]: {
          margin: 0,
          maxHeight: "100%"
        }
      },

      "& .closeButton": {
        position: "absolute",
        top: 40,
        right: 40,
        zIndex: 10,
        padding: theme.spacing(1.2),
        color: theme.palette.info.light,
        textTransform: 'unset',
        background: '#ededed',

        [theme.breakpoints.down('sm')]: {
          position: "fixed",
          top: 10,
          right: 10,
          padding: theme.spacing(0.5),
          backgroundColor: "#ededed"
        }
      },
    },

    dialogCard: {
      padding: `${theme.spacing(3)} ${theme.spacing(1.8)}`,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      boxShadow: "0px 3px 12px rgba(0, 0, 0, 0.07)",
      borderRadius: "20px",
      textAlign: 'center',

      '& .cardHeading': {
        fontSize: 18,
        fontWeight: 700,
        margin: 0,
        marginBottom: theme.spacing(0.5),
        color: theme.palette.info.light,
      },

      '& .cardDescription': {
        fontSize: 14,
        fontWeight: 500,
        margin: 0,
        marginBottom: theme.spacing(0.5),
        color: theme.palette.secondary.light,
      },

      '& .cardIcon': {
        marginBottom: theme.spacing(2),
      }
    }
  })
);

type DialogCardProps = {
  heading: string;
  description?: string;
  icon: React.ReactNode;
}

export const DialogCard = (props: DialogCardProps) => {
  const { icon, heading, description } = props;

  const classes = useStyles();

  return (
    <div className={classes.dialogCard}>
      <div className="cardIcon">
        {icon}
      </div>
      <h3 className="cardHeading">{heading}</h3>
      <If condition={!isEmptyOrNil(description)}>
        <p className="cardDescription">{description}</p>
      </If>
    </div>
  )
}

type LearnMoreDialogProps = {
  isOpen: boolean;
  onCancel: () => void;
  children?: React.ReactElement | React.ReactElement[];
  illustration?: string | React.ReactNode;
  heading?: string | React.ReactNode;
  subHeading?: string;
  title?: string;
  description?: string;
  cardsHeading?: string | React.ReactNode;
  isMultiColumn?: boolean;
  cardsList?: Array<{
    icon: React.ReactNode;
    heading: string;
    description?: string;
  }>;

};

const LearnMoreDialog = (props: LearnMoreDialogProps) => {
  const { isOpen, onCancel, heading, title, subHeading, isMultiColumn=true, cardsHeading, illustration, description, cardsList, children } = props;

  const classes = useStyles();

  const isTabletModeOnly = useMediaQuery((theme: Theme) => theme.breakpoints.between('sm', 'md'));

  return (
    <Dialog
      open={isOpen}
      onClose={onCancel}
      maxWidth="lg"
      className={classes.root}
      sx={{margin: 0, maxHeight: "unset"}}
    >
      <RewardsContentStyled className={classNames({'multiColumn': isMultiColumn})}>
        <Button onClick={onCancel} className="closeButton">
          <Close /> Close
        </Button>

        <If condition={!isTabletModeOnly}>
          {illustration && illustration}
        </If>
        <div className={classNames({'dialogContent': isMultiColumn})}>
          <If condition={!isEmptyOrNil(title)}>
            <p className="title">{title}</p>
          </If>

          <If condition={!isEmptyOrNil(heading)}>
            <h3 className="heading">{heading}</h3>
          </If>

          <If condition={!isEmptyOrNil(subHeading)}>
            <h4 className="subHeading">{subHeading}</h4>
          </If>

          <If condition={!isEmptyOrNil(description)}>
            <p className="description">
              {description}
            </p>
          </If>

          <If condition={!isEmptyOrNil(cardsHeading)}>
            <h4 className="contentHeading">
              {cardsHeading}
            </h4>
          </If>

          <If condition={!isEmptyOrNil(cardsList)}>
            <div className="featuresWrapper">
              {
                cardsList?.map((data, index) => (
                    <DialogCard
                      key={`card-${index}`}
                      {...data}
                    />
                  )
                )
              }
            </div>
          </If>

          {children && children}
        </div>

      </RewardsContentStyled>
    </Dialog>
  );
};

export default LearnMoreDialog;
