const RenderMerchantOrderingStatusBanner = (props: { merchant: Merchant, orderType: OrderType }) => {
  const { merchant, orderType } = props
  const isMerchantOrderingAvailable = isMerchantOnline(merchant, orderType);
  const isMerchanStoreOpen = isStoreOpen(merchant, new Date())

  const RenderOfflineMessage = () => {

    const variant = "subtitle2";
    const offlineStatusClasses = classNames(['offline-status'], {
      'ordering-unavailable': !isMerchantOrderingAvailable,
      'ordering-available': isMerchantOrderingAvailable
    })

    if (isKioskDevice()) {
      if (!isMerchantOrderingAvailable) {
        return (
          <Stack direction='row' className={offlineStatusClasses}>
              <Typography variant={variant} component='p'>
                {getOfflineMerchantMessage(orderType, merchant)}
              </Typography>
          </Stack>
        )
      } else {
        return null;
      }
    }

    if (isMerchantOrderingAvailable && isMerchanStoreOpen) return null;
    const openFromMinOffset = !isMerchanStoreOpen ? nextOpenHour(merchant) : null;
    const storeTimingsLabel = !isEmptyOrNil(openFromMinOffset)
      ? `Opens ${getTimeLabelFromOffset(openFromMinOffset)}`
      : ''

    return (
      <Stack direction='row' className={offlineStatusClasses}>
        <Switch>
          <Case condition={!isMerchanStoreOpen && storeTimingsLabel}>
            <CustomizedChip
              className='store-timings'
              label={storeTimingsLabel}
            />
            &nbsp;
            <Typography variant={variant} component='p'>
              <If condition={!isMerchantOrderingAvailable}>
                <Then>
                  Come back soon!
                </Then>
                <Else>
                  Order ahead available
                </Else>
              </If>
            </Typography>
          </Case>
          <Case condition={!isMerchantOrderingAvailable}>
            <Typography variant={variant} component='p'>
              {getOfflineMerchantMessage(orderType, merchant)}
            </Typography>
          </Case>
        </Switch>
      </Stack>
    )
  }

  return (
    <If condition={isMerchantOrderingAvailable && isMerchanStoreOpen && merchant.claimed}>
      <Then>
        <RenderWaitTimeSlider merchant={merchant} typographyProps={{ variant: "subtitle2", color: "textSecondary" }} short />
      </Then>
      <Else>
        <RenderOfflineMessage />
      </Else>
    </If>
  )
}

type MerchantInforHeader = {
  merchant: Merchant,
}
const MerchantInfoHeader = ({ merchant }: MerchantInforHeader): React.ReactElement => {
  const classes = useStyles()
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down("sm"));

  const { officialName, logo, category } = merchant;
  const { orderType, tableQrCodeObject } = useContext(AppContext);
  const [showDetailInfo, setShowInfo] = useState<boolean>(false);

  const AvatarEnhanced: any = withFallbackPath(Avatar, logo);
  const isKioskModeEnabled = isKioskDevice();
  const rewards = renderMerchantRewards(merchant);

  return (
    <div className={classes.root}>
      <CardHeader elevation={3}
        title={<Typography variant={isMobile ? "h6" : "h5"} component="div">{officialName}</Typography>}
        avatar={<AvatarEnhanced color="secondary" alt={officialName} src={`${logo}_small`} variant="rounded" />}
        action={
          <>
            <If condition={!isKioskModeEnabled}>
              <IconButton color="primary" className="filledIconButton" onClick={() => setShowInfo(true)}>
                <InfoIcon />
              </IconButton>
            </If>
            &nbsp;
            <FavoriteButton merchant={merchant} />
          </>
        }
        subheader={
          <Box sx={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', lineHeight: 1 }}>
            <Typography variant={isMobile ? "caption" : "body1"} color="textSecondary" className="category-line">
              {category}{rewards ? <>&nbsp;&nbsp;{"|"}&nbsp;&nbsp;</> : ''}
            </Typography>
            <Typography variant={isMobile ? "caption" : "body1"} color="textSecondary" className="category-line">
              {rewards}
            </Typography>
            <MerchantRatingInfo merchantRating={merchant?.rating} typographyProps={{ variant: (isMobile ? "caption" : "body1"), color: "textSecondary" }} starOnly />
          </Box>
        }
      />

      <Divider className="inDivider" />


      <div className="realTimeInfo">
        <If condition={tableQrCodeObject?.identifier}>
          <Then>
            <Typography color="textSecondary" component="div" variant={isMobile ? "caption" : "body1"} sx={{ display: 'flex', alignItems: 'center' }}>
              <img src={TableIcon} width={24} />&nbsp;Table: {tableQrCodeObject?.identifier}
            </Typography>
          </Then>
        </If>

        <RenderMerchantOrderingStatusBanner merchant={merchant} orderType={orderType} />
      </div>
      <MerchantInfoDrawer merchant={merchant} open={showDetailInfo} setOpen={setShowInfo} />
    </div>
  )
}

export default MerchantInfoHeader;
