import React, { useContext, useEffect } from 'react';
import { Button, createStyles, makeStyles, Theme, useMediaQuery } from '@material-ui/core';
import { pickAll, propOr } from 'ramda';
import { Link } from 'react-router-dom';
import useAxios from 'axios-hooks';
import { If } from 'react-if';
import { rgba } from 'polished';
import AliceCarousel from 'react-alice-carousel';

// src
import { FullscreenPaper } from '../bos_common/src';
import SimpleLoader from '../bos_common/src/components/SimpleLoader';
import { ClassifiedKey, ClassifiedsApiResponseType } from '../bos_common/src/types/ClassifiedType';
import 'react-alice-carousel/lib/alice-carousel.css';

import ClassifiedCarousel from '../components/Classifieds/ClassifiedCarousel';
import { FabContainer } from '../bos_common/src/components/FabContainers';
import { MenuDrawer } from '../components/common/MenuDrawer';
import { UserContext } from '../bos_common/src/context/UserContext';
import AppFooter from '../components/common/AppFooter';
import DiscoveryPageHeader from '../components/DiscoveryPageHeader';
import OrangeBubblesIllustrations from '../assets/images/banner/orange-bubbles.svg';
import GreenBubblesIllustration from '../assets/images/banner/green-bubbles.svg';
import RewardsOfferImg from '../assets/images/banner/deserve-rewards-landscape.png';
import RewardsOfferMobileImg from '../assets/images/banner/deserve-rewards-landscape-min.png';
import NoMarkupOfferImg from '../assets/images/banner/no-markup-offer.svg';
import MapColoredIcon from '../assets/icons/MapColoredIcon';
import Navbar from '../components/common/Navbar';
import DeserveRewardsDialog from '../components/DeserveRewardsDialog';
import NoMarkupDialog from '../components/NoMarkupDialog';
import { PIXEL_EVENTS_TYPE, trackRPEvent } from '../services/Tracking/FbPixelTracking';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    exploreButton: {
      '&.MuiButton-root': {
        background: rgba(theme.palette.secondary.light, 0.3),
        border: 0,
        padding: `${theme.spacing(1)} ${theme.spacing(2)}`,
        maxWidth: 650,

        '& .MuiButton-label': {
          textTransform: 'none',
          justifyContent: 'flex-start',
          fontSize: 14,
          color: theme.palette.secondary.light
        },

        '& .mapIcon': {
          marginLeft: 'auto',
        }
      },
    },

    root: {
      overflow: 'auto',
      position: 'relative',
      backgroundColor: 'white',
      padding: `${theme.spacing(2)} 0`,
      paddingTop: 0,

      '& .offersWrapper': {
        background: 'rgba(0, 0, 0, 0.02)',
        display: 'flex',
        justifyContent: 'center',

        '& .alice-carousel': {
          '& .alice-carousel__dots': {
            margin: 0,
          },
        },

        [theme.breakpoints.down('md')]: {
          flexDirection: 'column'
        },
      },

      "& .classified-sections-container": {
        width: '100%',
        margin: 'auto',
        boxSizing: 'border-box',
        padding: theme.spacing(2),

        [theme.breakpoints.down('md')]: {
          padding: theme.spacing(2),
        }
      },

      '& .browseMerchants': {
        border: `1px solid ${rgba(theme.palette.secondary.light, 0.5)}`,
        fontSize: 14,
        padding: theme.spacing(1),
        background: theme.palette.background.paper,
        textTransform: 'unset',
        marginTop: theme.spacing(2),

        '& .MuiButton-label': {
          color: theme.palette.info.light,
          display: 'flex',
          justifyContent: 'center',

          '& .MuiSvgIcon-root': {
            marginRight: theme.spacing(2),
          }
        }
      },
    },

    offerCard: {
      boxShadow: "0px 3px 12px rgba(0, 0, 0, 0.07)",
      borderRadius: '20px',
      margin: `${theme.spacing(2)} ${theme.spacing(2)} ${theme.spacing(2)} 0`,
      backgroundSize: 'auto 100%',
      backgroundPosition: 'right',
      backgroundRepeat: 'no-repeat',
      display: "grid",
      gridTemplateColumns: "1fr 1fr",

      '& .cardContent': {
        padding: `${theme.spacing(3)} 0 ${theme.spacing(3)} ${theme.spacing(3)}`,

        [theme.breakpoints.down('sm')]: {
          padding: `${theme.spacing(2)} ${theme.spacing(1)}`,
        }
      },

      '& .heading': {
        fontSize: 18,
        fontWeight: 700,
        margin: 0,
        marginBottom: theme.spacing(1),
        maxWidth: 166,
        width: "100%",
        color: theme.palette.info.light,
        minHeight: '85px',

        [theme.breakpoints.down('md')]: {
          fontSize: 14,
          minHeight: 60,
        },

        '& span': {
          color: theme.palette.secondary.main,
          '&.green': {
            color: '#22AC38'
          }
        }
      },

      '& .learnMore': {
        fontSize: '14px',
        display: 'flex',
        cursor: 'pointer',
      },

      '& .cardVisualContainer': {
        display: "flex",
        justifyContent: "right",
      },

      '& .cardThumbnail': {
        maxWidth: 285,
        height: '100%',
        borderRadius: '0 20px 20px 0',
        objectFit: 'cover',
        objectPosition: '85% center',
        maxHeight: 160,

        [theme.breakpoints.down('md')]: {
          maxWidth: 200,
          objectPosition: '58% 76%',
          maxHeight: 135,
        },

        [theme.breakpoints.down('sm')]: {
          maxWidth: 180,
          width: "100%",
          objectFit: 'contain',
          objectPosition: 'bottom right',
        },
      }
    },

    menuToggleButtonContainer: {
      position: 'fixed',
      bottom: 0,
      backgroundColor: 'transparent !important', // to override inline style
    },
  })
);

type OfferCardProps = {
  heading: string | React.ReactNode;
  imageSrc: string;
  illustrationsSrc: string;
  children: React.ReactElement;
}

const OfferCard = (props: OfferCardProps) => {
  const { heading, imageSrc, children, illustrationsSrc } = props;
  const classes = useStyles();

  return (
    <div className={classes.offerCard} style={{ backgroundImage: `url(${illustrationsSrc})` }}>
      <div className='cardContent'>
        <h3 className="heading">{heading}</h3>
        {children}
      </div>
      <div className="cardVisualContainer">
        <img src={imageSrc} className="cardThumbnail" />
      </div>
    </div>
  )
}

const SanMateoDiscoveryPage = () => {
  const classes = useStyles()
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
  const isTabletMode = useMediaQuery((theme: Theme) => theme.breakpoints.down('md'));

  const { user } = useContext(UserContext)

  const [{ data, loading }] = useAxios<ClassifiedsApiResponseType>(
    { url: `/classifieds/data`, method: "GET" },
  )

  const offersList = [
    <OfferCard
      heading={<>Supporting Local <span>Deserves Rewards</span></>}
      imageSrc={isMobile ? RewardsOfferMobileImg : RewardsOfferImg}
      illustrationsSrc={OrangeBubblesIllustrations}
      key={0}
    >
      <DeserveRewardsDialog />
    </OfferCard>,
    <OfferCard
      heading={<>Fee-Free Means <span className="green">No Markups</span></>}
      imageSrc={NoMarkupOfferImg}
      illustrationsSrc={GreenBubblesIllustration}
      key={1}
    >
      <NoMarkupDialog />
    </OfferCard>
  ]

  useEffect(() => {
    trackRPEvent(PIXEL_EVENTS_TYPE.visitedSanMateoMP, { user })
  }, [])

  return (
    <div className="container" style={{ minHeight: '90vh', maxWidth: '1080px', margin: 'auto', }}>
      <If condition={loading}>
        <SimpleLoader loading={loading} />
      </If>
      <If condition={!isMobile}>
        <Navbar>
          <Button
            variant="outlined"
            color="inherit"
            fullWidth
            component={Link}
            to={'/sanmateo/discovery'}
            size="large"
            className={classes.exploreButton}
          >
            Search in San Mateo... <MapColoredIcon className="mapIcon" />
          </Button>
        </Navbar>
      </If>
      <FullscreenPaper className={classes.root} elevation={0} sx={{ overflow: "unset", boxShadow: 'unset' }} square={true}>
        <DiscoveryPageHeader />

        <div className='classified-sections-container'>
          <ClassifiedCarousel key={ClassifiedKey.hotDeal} list={propOr([], ClassifiedKey.hotDeal, data)} />
        </div>
        <div className="offersWrapper">
          <div className='classified-sections-container'>
            <AliceCarousel
              autoHeight
              disableButtonsControls
              items={offersList}
              mouseTracking
              disableDotsControls={!isTabletMode}
              autoPlayInterval={2000}
              infinite={isTabletMode}
              responsive={{ 0: { items: 1 }, 800: { items: 2 } }}
              autoPlay={true}
            />
          </div>
        </div>
        <div className='classified-sections-container'>
          <ClassifiedCarousel key={ClassifiedKey.hotItem} list={propOr([], ClassifiedKey.hotItem, data)} />
          <ClassifiedCarousel key={ClassifiedKey.exclusive} list={propOr([], ClassifiedKey.exclusive, data)} />
          <ClassifiedCarousel key={ClassifiedKey.bigSavings} list={propOr([], ClassifiedKey.bigSavings, data)} />
          <ClassifiedCarousel key={ClassifiedKey.localGem} list={propOr([], ClassifiedKey.localGem, data)} />

          <Button
            variant="outlined"
            color="inherit"
            fullWidth
            component={Link}
            to={'/sanmateo/discovery'}
            size="medium"
            className="browseMerchants"
          >
            <MapColoredIcon className="mapIcon" /> Browse All Local Merchants in Map
          </Button>
        </div>
      </FullscreenPaper>

      <If condition={isMobile}>
        <FabContainer
          alignment='right'
          fabStyle={{ padding: "32px 16px 24px", background: 'linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%)', bottom: 0, }}
          fadeTop={isMobile}
          className={classes.menuToggleButtonContainer}
        >
          <MenuDrawer menuButtonProps={{ color: "primary", variant: "contained" }} />
        </FabContainer>
      </If>
      <AppFooter />
    </div>
  );
}

export default SanMateoDiscoveryPage;
