import React from "react";
import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import { PaymentMethod, StripeCardElementOptions } from "@stripe/stripe-js";
import PhoneInput from "react-phone-input-2";
import { If, Then } from "react-if";

import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Drawer from "@material-ui/core/Drawer";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import TextField from "@material-ui/core/TextField";
import Alert from "@material-ui/core/Alert";

import { createPaymentMethod } from "../../redux/slice/stripe/stripeAction";
import { useAppDispatch, useAppSelector } from "../../redux/hooks";
import { getStripeUpdatingStatus } from "../../redux/slice/stripe/stripeSelector";
import { PHONE_ONLY_COUNTRIES } from "../../config/constants";
import { isEmptyOrNil } from "../../utils";
import BottomDrawer from "../../bos_common/src/components/BottomDrawer";
import { Grid } from "@material-ui/core";

const options: StripeCardElementOptions = {
  style: {
    base: {
      fontSmoothing: "antialiased",
      fontSize: "16px",
    },
    invalid: {
      color: "#fa755a",
      iconColor: "#fa755a",
    },
  },
};

const useStyles = makeStyles((theme: Theme) => createStyles({
  root: {
    minHeight: '280px',
    padding: '0 1rem 1rem',

    "& .phone-input": {
      margin: `${theme.spacing(2)} 0`,

      "& .react-tel-input": {
        "& .flag-dropdown, & .flag-dropdown.open .selected-flag": {
          borderRadius: '6px 0 0 6px'
        },
        "& input": {
          width: "100%",
          height: "44px",
          borderRadius: "6px",
          border: "1px solid rgba(0, 0, 0, 0.15)",
        },
        "& .country-list": {
          borderRadius: '0px',
        },
        "& .phoneInput": {
          "&:focus": {
            borderColor: theme.palette.primary.main,
            boxShadow: "unset",
          },
        },
      },
      "& .helper-text": {
        color: 'rgba(0, 0, 0, 0.6)',
        fontSize: '0.75rem',
        marginTop: '3px'
      },
    },

    "& .phone-number-container": {
      marginTop: theme.spacing(4),
    },

    "& .react-tel-input .form-control": {
      width: '100%',
      borderColor: theme.palette.primary.main,
    },

    "& .credit-card-input": {
      margin: '1rem 0',
      padding: '1rem',
      border: `1px solid #c4c4c4`,
      borderRadius: '.5rem',
      "&.StripeElement--focus": {
        borderColor: theme.palette.primary.main,
        borderWidth: "2px",
        padding: 'calc(1rem - 1px)',
      },
    }
  }
}));

export interface AddPaymentFormDrawerProps {
  open: boolean;
  setOpen: (_: boolean) => void;
  hidePhoneNumber?: boolean;
  onPaymentMethodCreated?: (paymentMethod: PaymentMethod, phone?: string) => void;
}

export const AddPaymentFormDrawer = (props: Readonly<AddPaymentFormDrawerProps>): JSX.Element => {
  const { open, setOpen, hidePhoneNumber=true, onPaymentMethodCreated } = props;
  const classes = useStyles();
  const elements = useElements();
  const stripe = useStripe();
  const updating = useAppSelector(getStripeUpdatingStatus);
  const reduxDispatch = useAppDispatch();
  const [name, setName] = React.useState<string>('');
  const [phone, setPhoneNumber] = React.useState<string>('');
  const [error, setError] = React.useState<string>('');

  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = (e.target as HTMLInputElement).value;
    setName(value);
    setError("");
  }

  const handleSubmit = async (e: React.FormEvent) => {
    if (updating || !isValid()) {
      e.preventDefault();
      return;
    }

    e.preventDefault();

    try {
      const card = elements?.getElement(CardElement);

      if (!elements || !card || !stripe) {
        throw new Error("Could not connect to payment services");
      }

      reduxDispatch(createPaymentMethod({
        stripe,
        card,
        name,
      }))
        .then((res: any) => {
          if (isEmptyOrNil(res?.payload)) {
            setError("Could not add card, please check the information and try again");
            return;
          }

          const createdPaymentMethod = res.payload[0] as PaymentMethod;
          if (onPaymentMethodCreated) {
            onPaymentMethodCreated(createdPaymentMethod, phone);
          }
        });

    } catch (error) {
      throw new Error("Could not connect to payment services");
    }
  }

  const isValid = () => {
    if (!hidePhoneNumber && phone.length < 11) {
      setError("Phone number is required");
      return false;
    }

    if (name.trim().length < 1) {
      setError("Name is required");
      return false;
    }

    return true;
  }

  return (
    <BottomDrawer
      open={open}
      setOpen={setOpen}
    >
      <Paper className={classes.root}>
        <Grid container justifyContent="center">
          <Grid item xs={12} sm={6} lg={4}>
            <div className="credit-card-inputs-row">
              <form onSubmit={handleSubmit}>
                <Box style={{margin: '1rem 0'}}>
                  <Typography variant="h6" style={{ textAlign: 'center' }} mb="1">Add Payment Method</Typography>
                </Box>
                <Divider />
                <If condition={!hidePhoneNumber}>
                  <Then>
                    <Box style={{margin: '1rem 0'}}>
                      <PhoneInput
                        inputClass="phoneInput"
                        country={'us'}
                        onlyCountries={PHONE_ONLY_COUNTRIES}
                        value={phone}
                        specialLabel=""
                        placeholder="+1 (234) 567-8910"
                        onChange={p => {
                          setPhoneNumber(p);
                          setError("");
                        }}
                      />
                      <Typography variant="caption">Please add phone number to receive order updates, required</Typography>
                    </Box>
                    <Divider />
                  </Then>
                </If>
                <Box style={{margin: '1rem 0'}}>
                  <TextField id="outlined-basic" label="Name" variant="outlined" color="primary" onChange={handleChangeName} style={{ width: '100%' }} required />
                </Box>
                <CardElement
                  className="credit-card-input"
                  options={options}
                />
                <Box style={{margin: '1rem 0'}}>
                  {error && <Alert severity="warning" sx={{mb: 1}}>{error}</Alert>}
                  <Button color="primary" variant="contained" size="large" style={{ width: '100%' }} type="submit" disabled={updating}>Add</Button>
                </Box>
              </form>
            </div>
          </Grid>
        </Grid>
      </Paper>
    </BottomDrawer>
  );
}

export default AddPaymentFormDrawer;
