interface MerchandiseReviewsDrawerPageProps {
  merchant: Merchant | undefined;
  merchandise: Merchandise | null;
}

export default function MerchandiseReviewsDrawerPage(
  props: MerchandiseReviewsDrawerPageProps
) {
  const [filter, setFilter] = useState<string>("newest");

  const history = useHistory();
  const classes = useStyles();

  const { merchant, merchandise } = props;

  const onToggleReviewDrawer = () => {
    history.push(`/${merchant?.id}/item/${merchandise?.id}`);
  };

  const overallRatings = getMerchandiseOverallRatings(merchandise);

  const reviews = propOr([], "reviews")(merchandise);

  let filteredreviews = sort(descend(prop("createdAt")), reviews);

  if(filter !== "newest"){
    filteredreviews = filteredreviews.filter(
          (review: Review) =>{
            const reviewRating = pathOr(-1, ["rating", "overall"], review);
            const filterNum = +filter;
            const greaterRatingCheck = filterNum === 1 ? reviewRating.toFixed(2) >= (filterNum-1).toFixed(2) : reviewRating.toFixed(2) > (filterNum-1).toFixed(2);
            return greaterRatingCheck && reviewRating.toFixed(2) <= filterNum.toFixed(2)
          }
    );
  }

  if (isEmptyOrNil(reviews) || !overallRatings) {
    return null;
  }

  return (
    <BottomDrawer
      open={true}
      setOpen={onToggleReviewDrawer}
      className={classes.bottomDrawer}
      keepMounted={false}>
      <Box className={classes.root}>
        <ReviewDrawerHeader
          overallRating={overallRatings}
        />
        <Divider className="divider" />
        <Box className="content">
          <ReviewsFilter
              currentFilter={filter}
              setFilter={setFilter}
          />
          <If condition={filteredreviews.length === 0}>
            <Then>
              <Typography variant="body1">No Reviews Found</Typography>
            </Then>
            <Else>
              <Grid container className="reviewCardsContainer" spacing={2}>
                {filteredreviews.map((review: Review) => (
                  <Grid item xs={12} lg={6} key={review.id}>
                    {/* Placeholder to be added later */}
                    <LazyLoad overflow height={250} offset={250} >
                        <UserReviewCard review={review} />
                    </LazyLoad>
                  </Grid>
                ))}
              </Grid>
            </Else>
          </If>
        </Box>
      </Box>
    </BottomDrawer>
  );
}
