import { pickAll } from 'ramda';
import RP from 'react-facebook-pixel';

import config from '../../config/config';
import { isEmptyOrNil, isProduction } from '../../utils';
import { CapturedEventData } from './types';

export enum PIXEL_EVENTS_TYPE {
  visitedSanMateoMP = 'visited_san_mateo_marketplace',
  visitedSanMateoMPDiscovery =`visited_san_mateo_marketplace_discovery`
}

const advancedMatching = {}; // optional, more info: https://developers.facebook.com/docs/facebook-pixel/advanced/advanced-matching
const options = {
  autoConfig: true, // set pixel's autoConfig. More info: https://developers.facebook.com/docs/facebook-pixel/advanced/
  debug: true, // enable logs
};

const isPixelEnabled = () => isProduction() && !isEmptyOrNil(config.appConfig.pixelId)

export const initializeFbPixel = () => {
  if (!isPixelEnabled()) return
  RP.init(config.appConfig.pixelId, undefined, options)
  RP.pageView()
}

export const trackRPEvent = (title: PIXEL_EVENTS_TYPE, data: CapturedEventData = {}) => {
  if (!isPixelEnabled()) return

  const { user = {} } = data
  const _data = {
    user: pickAll(['id', 'email', 'phoneNumber', 'displayName'], user)
  }

  RP.trackCustom(title, _data)
}