type HeaderCardProps = {
  heading: string | React.ReactNode;
  subheading: string;
};

const HeaderCard = (props: HeaderCardProps) => {
  const { heading, subheading } = props;

  const [isDialogOpen, setDialogOpen] = useState(false);

  const classes = useStyles();

  return (
    <div className={classes.headerCard}>
      <div className="cardContent">
        <h3 className="heading">{heading}</h3>
        <p className="subheading">{subheading}</p>
        <Link className="learnMore" color={"inherit"} onClick={() => setDialogOpen(true)}>
          Learn More <ArrowRightAlt fontSize="small" sx={{ marginLeft: 1 }} />
        </Link>
      </div>

      <div className="cardFooter">
        <div className="iconWithText">
          <TelescopeIcon sx={{ marginRight: 1 }} /> Discovering{" "}
          <span>Local</span> at
        </div>
        <div className="locationChip">
          <LocationIcon /> San Mateo, CA
        </div>
      </div>

      <LearnMoreDialog
        isOpen={isDialogOpen}
        onCancel={() => setDialogOpen(false)}
        illustration={<img src={BannerImage} className="dialogImage" />}
        heading={<>Order Takeout with <span>In-Store Prices</span></>}
        subHeading="Discover local favorites completely fee-free"
        description="Tired of extra fees and markups? Skip the extra fees, in line wait,
        and phone calls, while earning rewards and saving big with special
        offers. Directly online order your local favorites with the best
        in-store price every time."
        cardsHeading="The best in-store price every time?"
        cardsList={[
          {
            heading: "No markups",
            description: "Order local online at the same unbeatable in-store price",
            icon: <CouponIcon />,
          },
          {
            heading: "No extra fees",
            description: "Our marketplace includes no service or commission fees",
            icon: <NoFeesIcon />,
          },
          {
            heading: "Exclusive deals",
            description: "Save more on your online orders with special offers only found on 1m.app",
            icon: <ExclusiveDealsIcon />,
          },
          {
            heading: "Rewards",
            description: "Earn rewards for shopping small that add up to bigger savings",
            icon: <RewardsIcon />,
          },
        ]}
      />
    </div>
  );
};

const DiscoveryPageHeader = () => {
  const classes = useStyles();
  const isTabletView = useMediaQuery((theme: Theme) => theme.breakpoints.down('md'));
  const isMobileView = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  return (
    <header className={classes.root}>
      <div className="headerContentWrapper">
        <HeaderCard
          heading={
            <>
              Order takeout with <span>in-store prices</span>
            </>
          }
          subheading="Discover local favorites, completely fee-free"
        />

       <If condition={isMobileView}>
          <Button
           variant="outlined"
           color="inherit"
           fullWidth
           component={RouterLink}
           to={'/sanmateo/discovery'}
           size="large"
           className='exploreButton'>
             Search in San Mateo... <MapColoredIcon className="mapIcon" />
          </Button>
        </If>

        <If condition={isTabletView}>
          <Then>
            <img src={BannerMobile} className="mobileBanner" alt="banner" />
          </Then>
          <Else>
            <NoFeesBubble />
          </Else>
        </If>
      </div>
    </header>
  );
};

export default DiscoveryPageHeader;
